.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.50);
}

.modal {

  text-align: center;
  font-family: 'Roboto Slab';

  position: fixed;
  top: 40%;
  left: 48%;
  transform: translate(-50%, -50%);
  display: inline-block;

  z-index: 100;
  overflow: hidden;
}

.buttonRow {
    display: flex;
    justify-content: center;
    align-items: center;
}

.button {
  padding: 2rem 2rem 2.1rem 2rem;
  font-weight: 700;
}
